.pagination
   margin-bottom: var(--mainMargin)
   &__inner
      width: 100%
      max-width: 370px
      margin: 0 auto
      display: flex
      justify-content: space-between
      align-items: center
      font-family: var(--robotoCond)
   &__desktop
      display: none
   &__list
      display: flex
   &__btn
      width: 50px
      min-width: 50px
      height: 50px
      padding: 5px
      display: flex
      justify-content: center
      align-items: center
      background: #fff
      @extend %transition
      &:hover,
      &:focus
         color: #fff
         background: var(--hoverColor)
         svg
            fill: #fff
      &--active
         color: var(--accent)
         cursor: auto
         &:hover,
         &:focus
            color: var(--accent)
            background: #fff
            svg
               fill: var(--hoverColor)
      &--arrow
         width: 40px
         min-width: 40px
         svg
            width: 18px
            height: 18px
   &__mob
      font-size: 24px

@media screen and (min-width: $tablet)
   .pagination
      &__inner
         max-width: 510px
         margin: 0
         justify-content: flex-start
         flex-wrap: wrap
      &__mob
         display: none
      &__desktop
         margin: 0 10px
         display: block
         font-weight: 700
         font-size: 16px
      &__list
         .pagination
            &__btn
               margin-right: 10px
               &:last-child
                  margin-right: 0
      &__page
         padding-top: 8px
