  padding-left: var(--mainMargin);
  padding-right: var(--mainMargin);
}
.compare__manage-arrow {
  width: 48px;
  min-width: 48px;
  height: 48px;
  background: var(--smallTitle);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.compare__manage-arrow:hover, .compare__manage-arrow:focus {
  background: var(--hoverColor);
}
.compare__manage-bullets {
  width: 100%;
  padding-top: 13px;
}
.compare__manage-bullets .compare__manage-pagination {
  display: flex;
  padding-left: var(--mainMargin);
  padding-right: var(--mainMargin);
}
.compare__manage .swiper-slide {
  width: auto;
}
.compare__slider {
  margin-bottom: var(--mainMargin);
  overflow: hidden;
  position: relative;
}
.compare__slider-prev, .compare__slider-next {
  top: 40%;
  bottom: auto;
}
.compare__slider-prev {
  left: 0;
}
.compare__slider-next {
  right: 0;
}
.compare__slider .shop__item {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .compare__manage-slider {
    padding: 0;
  }
  .compare__manage-swiper {
    display: flex;
  }
  .compare__manage-btn {
    margin-right: 12px;
  }
  .compare__manage-btn:last-child {
    margin-right: 0;
  }
  .compare__manage-mobile {
    order: -1;
  }
  .compare__manage-arrow {
    margin-left: auto;
    order: 1;
  }
  .compare__manage-bullets {
    display: none;
  }
}
.specific__title {
  font-family: var(--robotoCond);
  font-weight: 400;
  font-size: 28px;
  line-height: 110%;
  margin-bottom: var(--mainMargin);
}
.specific__col {
  margin-bottom: var(--mainMargin);
}
.specific__col:last-child {
  margin-bottom: 0;
}
.specific__top {
  margin-bottom: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.specific__name {
  padding-right: 10px;
}
.specific__toggle {
  width: 26px;
  min-width: 26px;
  height: 26px;
}
.specific__type {
  font-family: var(--robotoCond);
  font-weight: 400;
  font-size: 16px;
  color: var(--secondaryText);
}
.specific__num {
  width: 100%;
  padding: 12px;
  border-bottom: 1px solid #EBEBEB;
  display: none;
  flex-grow: 1;
}
.specific__num:nth-child(1) {
  display: block;
}
.specific__list {
  display: flex;
}
.specific__list--active {
  background: #F0F2F4;
  color: var(--accent);
}
.specific__item {
  margin-bottom: 12px;
}
.specific__item:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .specific__num:nth-child(2) {
    display: block;
  }
}
@media screen and (min-width: 1280px) {
  .specific__num {
    display: block;
  }
}
.navigate {
  width: 100%;
  position: fixed;
  z-index: 100;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  background: #fff;
}
.navigate__list {
  display: flex;
  justify-content: center;
}
.navigate__link {
  display: block;
  padding: 5px;
}
.navigate__link--active {
  color: var(--hoverColor);
}
.navigate__link--active svg {
  fill: var(--hoverColor);
}
.navigate__link:hover, .navigate__link:focus {
  color: var(--hoverColor);
}
.navigate__link:hover svg, .navigate__link:focus svg {
  fill: var(--hoverColor);
}
.navigate__top {
  width: 26px;
  margin: 0 auto;
  margin-bottom: 4px;
  position: relative;
}
.navigate__icon {
  width: 26px;
  height: 26px;
}
.navigate__title {
  font-weight: 400;
  font-family: var(--robotoCond);
  font-size: 12px;
}

@media screen and (min-width: 390px) {
  .navigate__item {
    min-width: 70px;
    margin-right: 10px;
  }
  .navigate__item:last-child {
    margin-right: 0;
  }
}
@media screen and (min-width: 768px) {
  .navigate {
    display: none;
  }
}